<template>
	<view class="fieldwork-list-container">
		<!-- 空状态 -->
		<view v-if="!data || data.length === 0" class="empty-state">
			<uni-icons type="folder-add" size="48" color="#cbd5e1" />
			<text class="empty-text">暂无建设内容信息</text>
			<text class="empty-tip">点击下方按钮添加</text>
		</view>

		<!-- 列表 -->
		<view v-else class="fieldwork-list">
			<view v-for="(item, index) in data" :key="item.id || index" class="fieldwork-item">
				<!-- 图片 -->
				<view v-if="item.photos && item.photos.length > 0" class="item-photos">
					<image v-for="(photo, pIndex) in item.photos.slice(0, 3)" :key="pIndex" :src="photo"
						class="item-photo" mode="aspectFill" @tap="previewImage(item.photos, pIndex)" />
					<view v-if="item.photos.length > 3" class="photo-more">
						+{{ item.photos.length - 3 }}
					</view>
				</view>

				<!-- 内容 -->
				<view class="item-content">
					<view class="item-field">
						<text class="field-label">项目名称：</text>
						<text class="field-value">{{ item.projectName || '未填写' }}</text>
					</view>
					<view class="item-field">
						<text class="field-label">建设内容：</text>
						<text class="field-value">{{ item.projectContent || '未填写' }}</text>
					</view>
					<view class="item-field">
						<text class="field-label">主体情况：</text>
						<text class="field-value">{{ item.mainStatus || '未填写' }}</text>
					</view>
				</view>

				<!-- 操作按钮 -->
				<view class="item-actions">
					<button class="action-btn action-btn--edit" @tap="$emit('edit', index)">
						<uni-icons type="compose" size="16" color="#166534" />
						<text>编辑</text>
					</button>
					<button class="action-btn action-btn--delete" @tap="confirmDelete(index)">
						<uni-icons type="trash" size="16" color="#dc2626" />
						<text>删除</text>
					</button>
				</view>
			</view>
		</view>

		<!-- 新增按钮 -->
		<view class="add-button-container">
			<button class="btn btn--primary" @tap="$emit('add')">
				<uni-icons type="plus" size="16" color="#ffffff" />
				<text>新增建设内容</text>
			</button>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'FieldworkBuildingList',
		props: {
			data: {
				type: Array,
				default: () => []
			}
		},
		methods: {
			previewImage(photos, current) {
				uni.previewImage({
					urls: photos,
					current: current
				})
			},
			confirmDelete(index) {
				uni.showModal({
					title: '确认删除',
					content: '确定要删除这条建设内容信息吗？',
					success: (res) => {
						if (res.confirm) {
							this.$emit('delete', index)
						}
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.fieldwork-list-container {
		padding: 24rpx 0;
	}

	.empty-state {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		padding: 80rpx 32rpx;
		color: #9ca3af;

		.empty-text {
			margin-top: 24rpx;
			font-size: 28rpx;
			color: #6b7280;
		}

		.empty-tip {
			margin-top: 12rpx;
			font-size: 24rpx;
		}
	}

	.fieldwork-list {
		display: grid;
		gap: 24rpx;
		grid-template-columns: 1fr;

		@media (min-width: 768px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	.fieldwork-item {
		background: #ffffff;
		border-radius: 12rpx;
		border: 2rpx solid #e5e7eb;
		overflow: hidden;
		transition: all 0.3s;

		&:hover {
			border-color: #166534;
			box-shadow: 0 4rpx 12rpx rgba(22, 101, 52, 0.1);
		}
	}

	.item-photos {
		display: flex;
		gap: 8rpx;
		padding: 16rpx;
		background: #f9fafb;
		position: relative;

		.item-photo {
			width: 160rpx;
			height: 160rpx;
			border-radius: 8rpx;
			object-fit: cover;
		}

		.photo-more {
			position: absolute;
			right: 24rpx;
			bottom: 24rpx;
			background: rgba(0, 0, 0, 0.6);
			color: #ffffff;
			padding: 8rpx 16rpx;
			border-radius: 8rpx;
			font-size: 24rpx;
		}
	}

	.item-content {
		padding: 24rpx;

		.item-field {
			display: flex;
			margin-bottom: 16rpx;
			font-size: 28rpx;

			&:last-child {
				margin-bottom: 0;
			}

			.field-label {
				color: #6b7280;
				flex-shrink: 0;
				min-width: 160rpx;
			}

			.field-value {
				color: #111827;
				flex: 1;
				word-break: break-all;
			}
		}
	}

	.item-actions {
		display: flex;
		gap: 16rpx;
		padding: 16rpx 24rpx;
		border-top: 2rpx solid #f3f4f6;

		.action-btn {
			flex: 1;
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 8rpx;
			padding: 16rpx;
			border-radius: 8rpx;
			font-size: 26rpx;
			border: none;
			background: transparent;

			&--edit {
				color: #166534;
				background: #f0fdf4;

				&:active {
					background: #dcfce7;
				}
			}

			&--delete {
				color: #dc2626;
				background: #fef2f2;

				&:active {
					background: #fee2e2;
				}
			}
		}
	}

	.add-button-container {
		margin-top: 32rpx;
		display: flex;
		justify-content: center;

		.btn {
			min-width: 300rpx;
		}
	}
</style>
